import React from 'react';
import styled, { css } from "styled-components";
import Button from 'material-ui/Button';
import GameMatch from './game-match';
import SearchIMG from "../img/search.gif";
import MeetingIMG from "../img/match.png";
import TitleIMG from '../img/title.png';
import OnlinePeers from "../img/Online-Peers.png";
import '../css/styles.css';

export default class GameLobby extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            status: "CHILLING",
            opponent: "none",
            opponentName: "none",
            peers: {},
            win: 0, //these are global to current
            lose: 0, //    session, in constrast to
            tie: 0, //    those on game-match
            key: 0,
            random: false
        };
        this.topicLobby = "vertex.rps.lobby.dev"; //TODO: change dinamically
        this.ipfs = this.props.session.ipfs;
        this.session = this.props.session;
        this.opponentsPreName = {}; //TODO: merge with opponentsName
        this.opponentsName = {}; //see line 40 & line 41

        this.handleRead = function(peerId, hash) {
            this.ipfs.files.cat(hash, (err, data) => {
                if (err) {
                    throw err;
                }
                let info = data.toString();
                this.opponentsName[peerId] = info;
            });
        }.bind(this);

        this.onPing = function(msg) {
            if (msg.from === this.props.peer.id) {
                return;
            }

            try {
                msg.data = JSON.parse(msg.data.toString());
            } catch (e) {
                return;
            }

            this.opponentsPreName[msg.from] = msg.data.usernameHash;
            this.handleRead(msg.from, msg.data.usernameHash);
            this.setState((prevState, props) => {
                prevState.peers[msg.from] = msg.data;

                if (
                    msg.data.status === "MATCHING" &&
                    prevState.status === "CHALLENGING" &&
                    prevState.opponent === msg.from &&
                    msg.data.opponent === this.props.peer.id
                ) {
                    console.log("Start matching", msg.from);
                    this.props.session.setStatus("PLAYING"); //change props of father
                    return { key: 1, opponent: msg.from, status: "MATCHING", peers: prevState.peers, opponentName: this.opponentsName[msg.from] };
                } else if (
                    msg.data.status === "CHALLENGING" &&
                    prevState.status === "CHALLENGING" &&
                    prevState.opponent === msg.from &&
                    msg.data.opponent === this.props.peer.id
                ) {
                    console.log("Start matching", msg.from);
                    this.props.session.setStatus("PLAYING"); //change props of father
                    return {
                        key:1,
                        opponent: msg.from,
                        status: "MATCHING",
                        peers: prevState.peers,
                        opponentName: this.opponentsName[msg.from]
                    };
                } else if (
                    msg.data.status === "MATCHING" &&
                    prevState.status === "CHALLENGING" &&
                    prevState.opponent === msg.from &&
                    msg.data.opponent !== this.props.peer.id
                ) {
                    console.log("waiting for matching ", msg.from);
                    return { key: 1, opponent: msg.from, status: "CHILLING", peers: prevState.peers, opponentName: this.opponentsName[msg.from] };
                }
                return { peers: prevState.peers,key:1 };
            });
        }.bind(this);

        this.ping = function() {
            var msg = ""; //this chunk set msg to be send
            if (this.state.status === "CHILLING") {
                msg = new Buffer(
                    JSON.stringify({
                        status: this.state.status,
                        usernameHash: this.props.usernameHash,
                        win: this.state.win,                                                                       //these are global to current
                        lose: this.state.lose,                                                                       //    session, in constrast to
                        tie: this.state.tie,
                        opponent: this.state.opponent,
                        opponentName: this.state.opponentName,
                    })
                );
            } else if (this.state.status === "CHALLENGING") {
                msg = new Buffer(JSON.stringify({
                        status: this.state.status,
                        usernameHash: this.props.usernameHash,
                        win: this.state.win,                                                                       //these are global to current
                        lose: this.state.lose,                                                                       //    session, in constrast to
                        tie: this.state.tie,
                        opponent: this.state.opponent,
                        opponentName: this.state.opponentName,
                    }));
            } else if (this.state.status === "MATCHING") {
                msg = new Buffer(JSON.stringify({
                        status: this.state.status,
                        usernameHash: this.props.usernameHash,
                        win: this.state.win,                                                                       //these are global to current
                        lose: this.state.lose,                                                                       //    session, in constrast to
                        tie: this.state.tie,
                        opponent: this.state.opponent,
                        opponentName: this.state.opponentName,
                    }));
            } else if (this.state.status === "WAITING") {
                msg = new Buffer(JSON.stringify({
                        status: this.state.status,
                        usernameHash: this.props.usernameHash,
                        win: this.state.win,
                        lose: this.state.lose, 
                        tie: this.state.tie,
                        opponent: this.state.opponent,
                        opponentName: this.state.opponentName
                    }));
            }
            this.ipfs.pubsub.publish(this.topicLobby, msg);
        }.bind(this);
    }

    componentWillMount() {
        setInterval(this.ping, 1000);
        console.log("ipfs.pubsub.subscribe: " + this.topicLobby);
        this.ipfs.pubsub.subscribe(this.topicLobby, this.onPing);
    }

    componentWillUnmount() {
        console.log("ipfs.pubsub.ubsubscribe: " + this.topicLobby);
        this.ipfs.pubsub.unsubscribe(this.topicLobby, this.onPing);
        clearInterval(this.ping);
    }

    challengePeer(peerId) {
        this.setState({
            opponent: peerId,
            status: "CHALLENGING"
        });
    }

    handleRandom(){
        for (var peerId in this.state.peers){
            if (this.state.peers[peerId].status === "CHILLING"){
                this.challengePeer(peerId);
                this.setState({random: true,status:"CHALLENGING"});
            }
        }
    }

    peer(peerId, index) {
        return (
            <div key={"peer" + (index + 1)} className="clickable">
                <br />
                <Button
                    disabled={
                        this.state.status === "CHALLENGING" ||
                        (this.state.peers[peerId].status === "CHALLENGING" &&
                            this.state.peers[peerId].opponent !==
                                this.props.peer.id) || this.state.random
                    }
                    onClick={() => this.challengePeer(peerId)}
                >
                    <TextBar>
                        {peerId in this.opponentsName
                            ? this.opponentsName[peerId]
                            : peerId}
                    </TextBar>
                </Button>
                <PeerButton
                    challenged={
                        this.state.peers[peerId].status === "CHALLENGING" &&
                        this.state.peers[peerId].opponent === this.props.peer.id
                    }
                >
                    {this.state.peers[peerId].status}
                </PeerButton>
            </div>
        );
    }

    reset(result) {
        this.session.setPoints(result); //change props of father
        this.session.setStatus("WAITING"); //change props of father
        this.setState((prevState, props) => ({
            status: "CHILLING",
            opponent: "none",
            opponentName: "none",
            key: 0,
            peers: {}
        }));
    }

    setWin() {
        this.setState((prevState, props) => ({ win: prevState.win + 1 }));
    }

    setLose() {
        this.setState((prevState, props) => ({ lose: prevState.lose + 1 }));
    }

    setTie() {
        this.setState((prevState, props) => ({ tie: prevState.tie + 1 }));
    }

    render() {
        return (
            <Div>
                
                {this.state.status === "MATCHING" ? (
                    <Main>
                        <GameMatch
                            ipfs={this.ipfs}
                            peerId={this.props.peer.id}
                            opponentId={this.state.opponent}
                            session={this}
                        />
                        
                    </Main>
                ) : (
                    <Aside>
                        {this.state.key === 0 ? (
                            <img
                                src={SearchIMG}
                                alt="Searching"
                                title="Searching"
                                style={{ width: "100%" }}
                            />
                        ) : (
                        <DivE>
                    
                   <img src={TitleIMG} className="imgD"  alt="Our AWESOME GAME!!"/> 
                
                        <TextContent>
                           Welcome{" "}
                             {this.props.session.state.name !== ""
                                ? this.props.session.state.name
                                : null}
                        </TextContent>
                        <br />
                            <ButtonSelection 
                                onClick={() => this.handleRandom()}
                            >
                                {(this.state.random)?
                            <TextContent>Opponent Picked, please wait... </TextContent>:
                            <TextContent2>LET OUR MONKEYS CHOOSE A MATCH FOR YOU </TextContent2>}
                            </ButtonSelection>
                            <FondoEm>
                            <div className="divPadding scroll">
                            
                                {Object.keys(this.state.peers).map(
                                    (peerId, index) => this.peer(peerId, index)
                                )}
                               
                            </div>
                            <br/><br/>
                            </FondoEm>
                      <DivB>
                      <TextContent3>
                      <br/>
                      <Span4>Opponent:
                             <Span4Inte>
                                {this.state.opponentName === "none"
                                    ? this.state.opponent
                                    : this.state.opponentName}
                             </Span4Inte>
                        </Span4>
                        <Span4L>My Lobby Status:
                            <Span4Inte>
                            {this.state.status}
                            </Span4Inte>
                        </Span4L>
                        <br/><br/>
                        </TextContent3>
                            <TextContent>
                                 <Span>Wins: {this.state.win} </Span>
                                 <Span>Loses: {this.state.lose} </Span>
                                 <Span>Ties: {this.state.tie} </Span>
                                 <Span2>Points: {this.props.session.state.points} </Span2>
                            </TextContent>
                            <br />
                       </DivB>
                    </DivE>
                        )}
                    </Aside>
                )}
               
            </Div>
        );
    }
}

const Div = styled.div`
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    background-color: Transparent;
    align-content:center;
    text-align: center;
`;

 const Span = styled.span`
    font-family: monospace, monospace;
    padding-left: 50px;
    padding-right: 20px;
    font-family: "Arial Black", Gadget, sans-serif;
    text-align: center;
 `;

 const Span2 = Span.extend`
    color: red;
    padding-left: 20px;
    padding-right: 50px;
    font-family: "Arial Black", Gadget, sans-serif;
    text-align: center;
 `;

const Span4 = Span.extend`
    padding-left: 16px;
    padding-right: 8px;
    font-family: "Arial Black", Gadget, sans-serif;
    text-align: center;
    size:12px;
`;

const Span4L = Span.extend`
    
    font-family: "Arial Black", Gadget, sans-serif;
    text-align: center;
`;

const Span4Inte = Span.extend`
    padding-left: 5px;
    text-align: center;
    
    font-family: monospace, monospace;
    font-size: 12px;

    @media (max-width: 732px) {
        font-size: 11px;
      }
    
    @media (max-width: 576px) {
        font-size: 11px;
      }
`;

const Main = styled.div`
    padding: 3px;
    width: 100%;
    padding-top: 10px;
    background-image: url(${MeetingIMG});
    background-size:cover;
 `;

const Aside = styled.div`
    width: 100vh;
    background-color: Transparent;
    display: flex;
    flex-direction: column;
    align-items: center;

    align-content: center;
    text-align: center;
 

 `;

const TextBar = styled.h2`
    font-size: 14px;
    font-family: monospace, monospace;
    text-align: center;
    @media (max-width: 723px) {
        font-size: 9px;
    }
`;

const TextContent = styled.h3`
    font-size: 18px;
    color: #eff2e4;
    font-weight: 400;
    font-family: sans-serif;
    text-align: center;
`;

const TextContent2 = styled.h3`
    font-size: 18px;
    color: #fff;
    font-weight: 600;
    font-family: "Arial Black", Gadget, sans-serif;
    text-align: center;
    
`;

const TextContent3 = styled.h3`
    font-size: 12px;
    color: #eff2e4;
    font-weight: 400;
    font-family: sans-serif;
    text-align: center;
`;

const PeerButton = styled.button`
    font-family: monospace, monospace;
    font-size: 10px;
    text-decoration: none;
    padding: 7px;
    font-weight: 400;
    color: #000000;
    background-color: #ffee03;
    margin-right: auto;

    @media (max-width: 300px) {
        padding: 0px;
    }
    ${props =>
        props.challenged &&
        css`
            background: palevioletred;
            color: white;
        `};
`;

 const DivB = styled.div`
    width: auto;
    background-color: #000;
    border-radius: 2px;
    align-content:center;
    text-align: center;
`;

const ButtonSelection = styled.button`
    background-color: #ff6c4c;
    padding: 20px;
    position: relative;
    width: 100%;
    font-size: 12px;
    text-decoration: none;
    color: #fff;
    background-image: linear-gradient(
        bottom,
        rgb(239, 241, 228) 0%,
        rgb(239, 241, 228) 100%
    );
`;

const DivE = styled.div`
    width: 100vh;
    align-content:center;
        text-align: center;
    
    
    @media (max-width: 732px) {
        width: 80%;
      }
    
    @media (max-width: 576px) {
        width:60%;
      }
`;

const FondoEm = styled.div`
    background-image: url(${OnlinePeers});
    height:355px;
    width:100vh;
    background-size:cover;
    
    @media (max-width: 732px) {
        height:auto;
        width:auto;
        background-size:cover;
      }
    
    @media (max-width: 576px) {
        height:auto;
        width:auto;
        background-size:cover;
      }
`;



